// src/ui/FeaturedPostCarousel.tsx
'use client'

import { useState, useEffect } from 'react'
import { ArrowLeftIcon, ArrowRightIcon } from '@heroicons/react/24/outline'
import { BlogPost } from '@/app/lib/types'


interface FeaturedPostCarouselProps {
  posts: BlogPost[]
}

export function FeaturedPostCarousel({ posts }: FeaturedPostCarouselProps) {
  const [activeIndex, setActiveIndex] = useState(0)

  useEffect(() => {
    const interval = setInterval(() => {
      setActiveIndex((prev) => (prev + 1) % posts.length)
    }, 5000)
    return () => clearInterval(interval)
  }, [posts.length])

  const goToPrevious = () => {
    setActiveIndex((prev) => (prev === 0 ? posts.length - 1 : prev - 1))
  }

  const goToNext = () => {
    setActiveIndex((prev) => (prev + 1) % posts.length)
  }

  return (
    <div className="relative w-full h-96 overflow-hidden rounded-lg">
      {/* Carousel Content */}
      <div
        className="flex transition-transform duration-500 ease-in-out"
        style={{ transform: `translateX(-${activeIndex * 100}%)` }}
      >
        {posts.map((post) => (
          <div
            key={post.id}
            className="w-full flex-shrink-0 relative h-96"
          >
            <img
              src={post.image}
              alt={post.title}
              className="w-full h-full object-cover brightness-75"
            />
            <div className="absolute bottom-0 left-0 right-0 p-8 text-white bg-gradient-to-t from-black/60 via-black/30 to-transparent">
              <h2 className="text-4xl font-bold mb-4">{post.title}</h2>
              <p className="text-lg">{post.excerpt}</p>
            </div>
          </div>
        ))}
      </div>

      {/* Navigation Arrows */}
      <button
        onClick={goToPrevious}
        className="absolute left-4 top-1/2 -translate-y-1/2 p-2 rounded-full bg-black/30 hover:bg-black/50 text-white"
      >
        <ArrowLeftIcon className="w-8 h-8" />
      </button>
      <button
        onClick={goToNext}
        className="absolute right-4 top-1/2 -translate-y-1/2 p-2 rounded-full bg-black/30 hover:bg-black/50 text-white"
      >
        <ArrowRightIcon className="w-8 h-8" />
      </button>

      {/* Dots Indicator */}
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex space-x-2">
        {posts.map((_, index) => (
          <button
            key={index}
            onClick={() => setActiveIndex(index)}
            className={`w-3 h-3 rounded-full ${
              index === activeIndex ? 'bg-white' : 'bg-white/50'
            }`}
          />
        ))}
      </div>
    </div>
  )
}